.detail-view {
    width: 36%;
    height: 100%;
    border-style: double;
    padding: 3px;
    margin-left: 50% auto;
    background-color: #f118188a;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .sprite-image {
    width: 58%;
    border-style: double;
    height: 150px;
    margin: 10px auto;
    background-color: #000000;
    border: 3px solid #ffffff;
    border-style: double;
    border-radius: 5px;
  }
  

  .data-wrapper {
    width: 90%;
    border-style: double;
    height: 110px;
    padding: 10px;
    margin: 40px auto;
    background-color: #091fe4;
    border-radius: 5px;
    box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.22);
    color: #FFF;
  }
  
  .data-name {
    margin: 0 0 5px;
  }
  
  .data-char {
    font-size: 15px;
  }
