body {
  margin: 0%;
  text-transform: capitalize;
  padding: 150px;
  font-family: 'Press Start 2P', cursive;
  font-size: 45%;
  background-image: url(/Pokedex/static/media/background.a4bbd891.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  object-fit:cover;
  background-size: 100%;
  background-color: rgb(255, 255, 255);
  padding-top: 10%;
  overflow: hidden;

}

/* width */
::-webkit-scrollbar {
  width: 18px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 8px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(255, 0, 0);
  border-radius: 10px;
}

h1,
h2,
h3,
h4,
p {
  font-size: 20px;
  margin: 0;
}
